
import { Options, Vue } from 'vue-class-component';
import LoginRequest from '@/models/LoginRequest';
import { login } from '@/services/api-service';
import { useStore } from 'vuex';
import { required, minLength } from '@vuelidate/validators';
import useVuelidate from '@vuelidate/core';

@Options({
  components: {},
  validations: {
    login: {
      required,
      minLength: minLength(3),
    },
    password: {
      required,
      minLength: minLength(8),
    },
  },
})
export default class LoginView extends Vue {
  isLoading = false;

  serverErrors: string[] = [];

  login = '';

  password = '';

  v$ = useVuelidate();

  store = useStore();

  errors: { [key: string]: string } = {
    'login.required': 'Обязательное поле',
    'password.required': 'Обязательное поле',
    'login.minLength': 'Минимальная длина 3 символа',
    'password.minLength': 'Минимальная длина 8 символов',
    'user not found': 'Пользователь не найден',
    'wrong password': 'Неверный пароль',
  };

  getError(errorKey: string): string {
    return this.errors[errorKey];
  }

  async signIn(): Promise<void> {
    const loginRequest: LoginRequest = {
      login: this.login,
      password: this.password,
    };

    try {
      this.isLoading = true;
      this.serverErrors = [];
      const user = await login(loginRequest);
      if (user) {
        console.log(user);

        this.store.commit('setUser', user);
        this.$router.push({ name: 'home' });
      }
    } catch (ex: any) {
      if (ex) {
        this.serverErrors.push(ex.message);
      }
    } finally {
      this.isLoading = false;
    }
  }
}
